
import {mapGetters, mapMutations} from 'vuex'
import XImg from 'vux/src/components/x-img/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

export default {
  head: {
    title: {
      inner: ''
    }
  },
  components: {
    XImg,
    Scroller,
    Loading
  },
  created () {
    this.getOfferList(this)
  },
  mounted () {
    this.height = document.querySelector('.content').clientHeight + 'px'
  },
  methods: {
    handleSaveData (item) {
      this.$localStorage.set('orderCompany', JSON.stringify({
        companyId: item.id,
        banner: item.bigPic,
        companyName: item.name
      })) // 
    },
    ...mapMutations({
      showLoading: 'updateLoadingStatus',
      getOfferList: 'getOfferList'
    })
  },
  data () {
    return {
      height: '0px'
    }
  },
  computed: {
    ...mapGetters({
      list: 'getOfferList'
    })
  }
}



